<template>
  <div class="ui-page ui-page-full"
    @touchstart="handleTouchStart"
    @touchend="handleTouchEnd">

    <div class="ui-header">
      <ui-button text="Home" icon="fas fa-arrow-left" @click="$router.push('/');"/>
      <h2>{{ dateString }}</h2>
      <ui-button icon="fas fa-chevron-left" @click="changeDate(-1)" />
      <ui-button icon-right="fas fa-chevron-right" @click="changeDate(1)" />
    </div>

    <!-- <div class="ui-rows"> -->
      <div class="ui-row" v-for="(r, i) in timeline" :key="i" @click="to(r.id)">
        <span>{{ r.t0 }}</span>
        <i :class="r.icon"></i>
        <div>{{ r.text }}</div>
        <span v-if="r.type === 'span'">{{ duration(r) }}</span>
        <span v-if="r.type === 'span'">{{ r.t1 }}</span>
      </div>
    <!-- </div> -->
    <!-- <div class="ui-spacer-v"></div> -->
    <!-- <div class="ui-spacer-v"></div> -->
    <!-- <ui-button is-full text="Add" icon-right="fas fa-plus" @click="to(-1)" /> -->
    <ui-fab icon="fas fa-plus" @click="to(-1)" />

  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    date: {
      typs: String,
      required: true,
    },
  },
  data() {
    return {
      touch: { x: 0, y: 0 },
    };
  },
  watch: {
    $route(to) {
      // window.console.log(to);
      this.show(to.params.date);
    },
  },
  computed: {
    ...mapState(['dates', 'items']),
    dateString() {
      const w = (new Date(this.date)).getDay();
      const ws = ''.split('')[w];
      return `${this.date} ${ws}`;
    },
    timeline() {
      return this.dates.map((el) => ({ ...el, ...this.items.find((o) => o.code === el.code) }));
    },
  },
  methods: {
    handleTouchStart(e) {
      this.touch.x = e.changedTouches[0].clientX;
      this.touch.y = e.changedTouches[0].clientY;
    },
    handleTouchEnd(e) {
      const dx = this.touch.x - e.changedTouches[0].clientX;
      const dy = this.touch.y - e.changedTouches[0].clientY;
      const delta = Math.abs(dy / dx);
      if (!Number.isNaN(delta) && Math.abs(dy / dx) < 0.3) {
        this.changeDate(dx > 0 ? 1 : -1);
      }
    },
    changeDate(num) {
      let d = new Date(this.date);
      d = new Date(d.getFullYear(), d.getMonth(), d.getDate() + num + 1);
      const ds = d.toJSON().split('T').shift();
      this.$router.push(`/date/${ds}`);
      window.scrollTo(0, 0);
      this.show(ds);
    },
    show(ds) {
      this.$store.dispatch('act', { action: 'READ', data: { date: ds } }).then(() => {
        window.console.log('show', this.date, [...this.dates]);
      });
    },
    duration(obj) {
      if (obj.type !== 'span') return '';
      const delta = new Date(`${obj.date} ${obj.t1}`) - new Date(`${obj.date} ${obj.t0}`);
      const h = Number.parseFloat(delta / (60 * 60 * 1000)).toFixed(1);
      return delta > 3600000 ? `${h}h` : `${delta / 60000}m`;
    },
    to(id) {
      this.$router.push(`/date/${this.date}/${id}`);
    },
  },
  created() {
    this.show(this.$route.params.date);
  },
};
</script>
