import Vue from 'vue';
import Vuex from 'vuex';

import Dexie from 'dexie';
import items from './items';

Vue.use(Vuex);

Dexie.delete('tt');

class Database extends Dexie {
  constructor() {
    super('tt3');
    this.version(1).stores({
      dates: '++id,date',
    });
    this.t_dates = this.table('dates');
  }

  async read(dat) {
    window.console.log('db.read', dat.date);
    const arr = await this.t_dates.where('date').equals(dat.date).sortBy('t0');
    return arr;
  }

  create(dat) { this.t_dates.add(dat); }

  update(dat) { this.t_dates.update(dat.id, dat); }

  delete(dat) { this.t_dates.delete(dat.id); }

  async serialize() {
    const dates = await this.t_dates.toArray();
    return JSON.stringify(dates);
  }
}

const db = new Database();

export default new Vuex.Store({
  strict: true,
  state: {
    items,
    dates: [],
    error: {},
  },
  getters: {
  },
  mutations: {
    closeError(state) {
      state.error.visible = false;
    },
    setError(state, err) {
      state.error = { ...err, visible: true };
    },
    setDates(state, data) {
      state.dates = data;
    },
  },
  actions: {
    async importData() {
      // window.fetch({}).then((res) => res.json())
    },
    async exportData() {
      // window.fetch({}).then((res) => res.json())
      const jsonStr = JSON.stringify(await db.t_dates.toArray());
      window.console.log(jsonStr.length, jsonStr);
    },
    async act({ commit }, { action, data }) {
      // window.console.log('act', { action, data });
      let res = [];
      try {
        switch (action) {
          case 'READ':
            res = await db.read(data);
            // commit('updateState', { action, payload: res, data });
            commit('setDates', res);
            break;
          case 'CREATE':
            await db.create(data);
            break;
          case 'UPDATE':
            await db.update(data);
            break;
          case 'DELETE':
            await db.delete(data);
            break;
          default:
            throw new Error(`the action "${action}" is invalid.`);
        }
      } catch (e) {
        window.console.log('error', e);
        commit('setError', e);
      }
    },
  },
});
