<style>
button, .ui-button {
  box-sizing: border-box; min-width: 44px; height: 36px; padding: 0 12px;
  /* background-color: #bbc; border: 1px solid #ddd; */
  background-color: #d0d0d9; border: 1px solid #d0d0d9; color: #009;
  /* background-color: #f0f0f0; border: 1px solid #eee; color: #009; */
  /* background-color: var(--bg-color); border: 1px solid #fff; color: #009; */
  border-radius: 4px; outline: none; font-size: 14px; white-space: nowrap;
  text-overflow: ellipsis;
}
button>span, button>i { font-size: inherit; color: inherit; }
button+button { margin: 0 0 0 4px;}
span+i, i+span { padding: 0 0 0 4px; }
.ui-button-full { width: 100%; }
</style>

<template>
  <button :class="classes" @click="$emit('click')">
    <i v-if="icon" :class="icon"></i>
    <span v-if="text">{{ text }}</span>
    <i v-if="iconRight" :class="iconRight"></i>
  </button>
</template>

<script>
export default {
  name: 'UiButton',
  props: {
    text: String,
    icon: String,
    iconRight: String,
    isFull: Boolean,
  },
  computed: {
    classes() {
      return {
        'ui-button': true,
        'ui-button-full': this.isFull,
      };
    },
  },
  methods: {},
};
</script>
