<style>
.ui-input {
  box-sizing: border-box; width: 100%; height: 32px; padding: 0 8px;
  background-color: #f0f0f6; border: 0px solid #ccc; border-radius: 4px;
  outline: none; font-size: 16px;;
}
[type=number] { text-align: right; }
[type=date] { width: 120px; }
[type=time] { width: 90px; }
[type=datetime-local] { width: 120px; }
[type=color] { width: 64px; }
/* .ui-input-full { width: 100% !important } */
</style>

<template>
  <input
    :class="classes"
    :type="type"
    :value="value"
    :placeholder="placeholder"
    @input="$emit('input', $event.target.value)">
</template>

<script>
export default {
  name: 'UiInput',
  props: {
    type: {
      type: String,
      default: 'text',
      validator(val) {
        return [
          'text',
          'password',
          'email',
          'url',
          'number',
          'time',
          'date',
          'datetime',
          'datetime-local',
          'color',
        ].indexOf(val) !== -1;
      },
    },
    value: String,
    placeholder: String,
    isFull: Boolean,
  },
  computed: {
    classes() {
      return {
        'ui-input': true,
        'ui-input-full': this.isFull,
      };
    },
  },
  methods: {},
};
</script>
