<style>
.ui-select {
  box-sizing: border-box; height: 32px; padding: 0 8px;
  background-color: #f0f0f6; border: 0px solid #ccc; border-radius: 4px;
  outline: none; font-size: 16px;;
}
</style>

<template>
  <select class="ui-select" @change="$emit('select', $event.target.value)">
    <option
      v-for="(opt, i) in options"
      :key="i"
      :value="opt.value"
      :selected="opt.value === value">
      {{ opt.text }}
    </option>
  </select>
</template>

<script>
export default {
  name: 'UiSelect',
  props: {
    value: {
      type: String,
    },
    options: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  computed: {},
  methods: {},
  created() {
    window.console.log({ value: this.value });
  },
};
</script>
