<template>
  <div class="ui-page">

    <div class="ui-header">
      <ui-button text="" icon="fas fa-arrow-left" @click="$router.back()" />
      <h2></h2>
      <ui-button v-if="!isNew" text="" icon-right="far fa-trash-alt"
        @click="confirm.visible = true" />
    </div>

    <div class="ui-item">
      <div class="ui-label"></div>
      <div class="ui-data">
        <ui-select :options="options" v-model="obj.code" @select="select"></ui-select>
      </div>
    </div>

    <div class="ui-item">
      <div class="ui-label"></div>
      <div class="ui-data">
        <div class="ui-flex">
          <ui-input type="time" v-model="obj.t0" />
          <div v-if="isSpan"></div>
          <ui-input v-if="isSpan" type="time" v-model="obj.t1"/>
        </div>
      </div>
    </div>

    <div class="ui-item" v-if="isForeBack">
      <div class="ui-label"></div>
      <div class="ui-data">
        <!-- <p style="font-size: 14px"> </p> -->
        <div class="ui-flex">
          <ui-button text="" />
          <ui-button text="" />
        </div>
      </div>
    </div>

    <div></div>
    <textarea v-model="obj.desc"></textarea>

    <div class="ui-spacer-v"></div>

    <ui-button is-full text="" icon="fas fa-save"
      @click="command(isNew ? 'CREATE' : 'UPDATE')" />

    <ui-confirm v-show="confirm.visible" title="Delete"
      text="Are you sure to delete this data?"
      @cancel="confirm.visible = false"
      @ok="command('DELETE')">
    </ui-confirm>

    <!-- <pre>{{ JSON.stringify(obj, null, 2) }}</pre> -->

  </div>
</template>

<script>
import { mapState } from 'vuex';

function hhmm() {
  const d = new Date();
  const hh = `0${d.getHours()}`.slice(-2);
  const mm = `0${d.getMinutes()}`.slice(-2);
  return `${hh}:${mm}`;
}

class Dat {
  constructor(obj) {
    const o = obj || {};
    this.id = o.id || -1;
    this.date = o.date || '';
    this.code = o.code || 'sleep';
    this.t0 = o.t0 || hhmm();
    this.t1 = o.t1 || hhmm();
    this.desc = o.desc || '';
  }
}

export default {
  data() {
    return {
      isNew: false,
      isSpan: true,
      obj: {},
      confirm: {
        visible: false,
      },
    };
  },
  computed: {
    ...mapState(['dates', 'items']),
    options() {
      return this.items.map((el) => ({ text: el.text, value: el.code }));
    },
    isForeBack() {
      return this.isSpan && this.obj.t0 > this.obj.t1;
    },
  },
  methods: {
    select(val) {
      this.obj.code = val;
      this.isSpan = this.items.find((el) => el.code === val).type === 'span';
      window.console.log(val, { ...this.obj });
    },
    command(type) {
      if (type === 'CREATE') delete this.obj.id;
      this.$store.dispatch('act', { action: type, data: this.obj }).then(() => {
        this.$router.back();
      });
    },
  },
  created() {
    const id = Number(this.$route.params.id);
    this.isNew = id < 0;
    this.obj = id < 0 ? new Dat() : new Dat(this.dates.find((el) => el.id === id));
    this.obj.date = this.$route.params.date;
  },
};
</script>
